import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import { Mutation } from 'react-apollo';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Form } from 'informed';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputAdornment from '@material-ui/core/InputAdornment';
import Text from '../formComponents/Text';
import { CREDIT } from '../../graphql/mutation/remote';

const styles = {
    progress: {
        margin: "1rem",
    }
}

const defaults = {
    amount: {
        type: "number",
        default: 0,
        startAdornment: <InputAdornment position="start"></InputAdornment>
    }
};

class FormDialog extends React.Component {
    state = {
        open: false,
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    handleOpen = () => {
        this.setState({ open: true });
    };

    render() {
        const { open } = this.state;
        const { history, classes, negativeHandler = e => this.handleClose(), positiveHandler = e => null, postMutation = e => null } = this.props;
        const title = "Add Credit", content = "Add credit amount to this user.";
        return (
            <div>
                <Mutation mutation={CREDIT}>
                    {(mutate, { loading, error, data }) => {
                        return (
                            <React.Fragment>
                                <IconButton aria-label="Credit" onClick={this.handleOpen}>
                                    <AttachMoneyIcon />
                                </IconButton>
                                {
                                    open ? (
                                        <Dialog
                                            fullScreen={false}
                                            open={open}
                                            onClose={negativeHandler}
                                            aria-labelledby={title}
                                        >
                                            <Form onSubmit={e => {
                                                console.log("form submitted", e);
                                                positiveHandler(mutate, e)
                                                    .then((data) => {
                                                        if (postMutation) {
                                                            postMutation(data);
                                                            this.handleClose(e);
                                                        }
                                                        else {
                                                            history.goBack()
                                                        }
                                                    })
                                            }}>
                                                <DialogTitle id={title}>{title}</DialogTitle>
                                                <DialogContent>
                                                    <DialogContentText>
                                                        {content}
                                                        {
                                                            <Text
                                                                field={"amount"}
                                                                type={defaults.amount.type || "text"}
                                                                step={0.01}
                                                                validate={defaults.amount.validate}
                                                                initialValue={defaults.amount.default}
                                                                startAdornment={defaults.amount.startAdornment}
                                                                validateOnChange
                                                                validateOnBlur
                                                            >
                                                            </Text>
                                                        }
                                                    </DialogContentText>
                                                </DialogContent>
                                                <DialogActions>
                                                    {
                                                        loading ? <CircularProgress className={classes.progress} /> : null
                                                    }
                                                    <Button onClick={negativeHandler} color="primary" autoFocus>Cancel</Button>
                                                    <Button size="large" type="submit" disabled={loading}>Confirm</Button>
                                                </DialogActions>
                                            </Form>
                                        </Dialog>
                                    ) : null

                                }

                            </React.Fragment>
                        );
                    }}
                </Mutation>
            </div>
        );
    }
}

export default withStyles(styles)(FormDialog);


