import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { Link } from 'react-router-dom';

import { Query, Mutation } from 'react-apollo';
import { SITE_ENTRIES } from '../graphql/query/remote';
import { SITEENTRYTABLE as SITEENTRYTABLE_QUERY } from '../graphql/query/local';
import { SITEENTRYTABLE } from '../graphql/mutation/local';
import { DELETESITEENTRY } from '../graphql/mutation/remote';

import EnhancedTable from '../components/EnhancedTable';
import { defaultState } from '../graphql/resolvers';
import commanRender from '../utils/commanRender';

const styles = theme => ({
    root: {
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 2,
        paddingBottom: theme.spacing.unit * 2,
    },
    fab: {
        position: 'fixed',
        bottom: theme.spacing.unit * 2,
        right: theme.spacing.unit * 2,
    },
    container: {
        width: '96%'
    }
});

class SiteDetail extends Component {
    componentWillMount() {
        this.setState({
            reset: true
        });
    }
    render() {
        const { id } = this.props.match.params;
        const { classes, history, user } = this.props;
        const fab = {
            color: 'primary',
            className: classes.fab,
            icon: <AddIcon />,
        };

        const columnData = [
            { id: 'createdAt', numeric: false, disablePadding: true, colSpan: 1, label: 'Created At' },
            { id: 'mistri', numeric: false, disablePadding: false, colSpan: 2, label: 'MISTRI (Quantity & Price)' },
            { id: 'labour', numeric: false, disablePadding: false, colSpan: 2, label: 'LABOUR (Quantity & Price)' },
            { id: 'eit', numeric: false, disablePadding: false, colSpan: 2, label: 'EIT (Quantity & Price)' },
            { id: 'morang', numeric: false, disablePadding: false, colSpan: 2, label: 'MORANG (Quantity & Price)' },
            { id: 'githi', numeric: false, disablePadding: false, colSpan: 2, label: 'GITHI (Quantity & Price)' },
            { id: 'cement', numeric: false, disablePadding: false, colSpan: 2, label: 'CEMENT (Quantity & Price)' },
            { id: 'saria', numeric: false, disablePadding: false, colSpan: 2, label: 'SARIA (Quantity & Price)' },
            { id: 'dust', numeric: false, disablePadding: false, colSpan: 2, label: 'DUST (Quantity & Price)' },
            { id: 'other', numeric: false, disablePadding: false, colSpan: 2, label: 'OTHER (Quantity & Price)' },
            { id: 'managerTotal', numeric: false, disablePadding: false, colSpan: 1, label: 'MANAGER TOTAL ()' },
            { id: 'total', numeric: false, disablePadding: false, colSpan: 1, label: 'TOTAL ()' },
        ];
        const exclude = ["id", "count", "createdAt", "updatedAt", "__typename"];
        return (
            <Mutation mutation={SITEENTRYTABLE}>
                {(lmutate, args) => {
                    if (this.state.reset) {
                        lmutate({ variables: defaultState.siteEntryTable });
                        this.setState({ reset: false });
                    }
                    return (
                        // <Comp {...data} {...props} ></Comp>
                        <Query query={SITEENTRYTABLE_QUERY}>
                            {({ loading, error, data: { siteEntryTable } }) => {
                                if (loading) {
                                    return (<span>loading...</span>);
                                }
                                else if (error) {
                                    return (<span>Error Occured!</span>);
                                }
                                const { page, rowsPerPage } = siteEntryTable;
                                // console.log("siteEntryTable...", siteEntryTable);
                                return (
                                    <Query query={SITE_ENTRIES} variables={{ id, limit: rowsPerPage, skip: page * rowsPerPage }}>
                                        {({ loading, error, data: { siteEntries }, refetch }) => {
                                            // refetch();
                                            if (loading) {
                                                return (<span>loading...</span>);
                                            }
                                            else if (error) {
                                                return (<span>Error Occured!</span>);
                                            }
                                            return (
                                                <div>
                                                    {/* <Typography>
                                                            {JSON.stringify(mutate({args: {page: 2}}))}
                                                        </Typography> */}
                                                    <Paper className={classes.root}>
                                                        <Grid container spacing={32}>
                                                            <Grid item container direction="column" sm={4}>
                                                                <Grid item>
                                                                    <Typography variant="headline" className="ttc" color="primary">
                                                                        Name: {siteEntries.name}
                                                                    </Typography>
                                                                </Grid>
                                                                <Grid item>
                                                                    <Typography variant="headline" className="ttc" color="secondary">
                                                                        Location: {siteEntries.location}
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                            <Grid item container direction="column" sm={4}>
                                                                <Grid item>
                                                                    <Typography variant="subheading">
                                                                        Total Cost  (): {siteEntries.cost}
                                                                    </Typography>
                                                                </Grid>
                                                                <Grid item>
                                                                    <Typography variant="subheading">
                                                                        Total Entry: {siteEntries.count}
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                            <Grid item container direction="column" sm={4}>
                                                                <Grid item>
                                                                    <Typography variant="subheading">
                                                                        Manager : {siteEntries.manager.name}
                                                                    </Typography>
                                                                </Grid>
                                                                <Grid item>
                                                                    <Typography variant="subheading">
                                                                        Manager Spent Amount:  {siteEntries.managerSpentAmount}
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>
                                                        </Grid>
                                                    </Paper>
                                                    <Grid container className={classes.container}>
                                                        <Grid item xs={12} sm={12}>
                                                            <Mutation mutation={DELETESITEENTRY}>
                                                                {(deleteMutate, { loading, error }) => {
                                                                    if (loading) {
                                                                        return (<span>deleting...</span>);
                                                                    }
                                                                    else if (error) {
                                                                        return (<span>Error Occured!</span>);
                                                                    }
                                                                    return (
                                                                        <EnhancedTable {...this.props}
                                                                            data={siteEntries.entries}
                                                                            refetch={refetch}
                                                                            count={siteEntries.count}
                                                                            settings={siteEntryTable}
                                                                            title="Entry"
                                                                            columnData={columnData}
                                                                            exclude={exclude}
                                                                            options={{
                                                                                onEdit: (user && user.role === "1") ? (async args => {
                                                                                    history.push(`/makeEntry/${id}/edit/${args}`);
                                                                                }) : (null)
                                                                            }}
                                                                            // onDelete={
                                                                            //     async args => {
                                                                            //         console.log("calling deleteMutate function...");
                                                                            //         await deleteMutate({ variables: Object.assign({}, args, { siteId: id }) });
                                                                            //         await lmutate({ variables: defaultState.siteEntryTable });
                                                                            //         refetch();
                                                                            //     }
                                                                            // }
                                                                            render={commanRender("managerSpentAmount")}
                                                                            mutate={async args => {
                                                                                await lmutate({ variables: args });
                                                                                // refetch();
                                                                            }}>
                                                                        </EnhancedTable>
                                                                    );
                                                                }}
                                                            </Mutation>
                                                        </Grid>
                                                    </Grid>
                                                    {/* { siteEntries.entries.length ? <Table data={siteEntries.entries}></Table> : null} */}
                                                    <Button variant="fab" className={fab.className} color={fab.color} component={Link} to={`/makeEntry/${id}`}>
                                                        {fab.icon}
                                                    </Button>
                                                </div>
                                            );
                                        }}
                                    </Query>
                                );
                            }}
                        </Query>
                    );
                }}
            </Mutation>
        );
    }
}

export default withStyles(styles)(SiteDetail);



