import React from 'react';
import TableCell from '@material-ui/core/TableCell';
export default (match, bgcolor = "rgba(0, 255, 0, 0.5)") => (key = null, items, colorValue) => {
    console.log(key, items);
    if (!(items instanceof Object)) {
        return (
            <TableCell style={{ color: colorValue, backgroundColor: key === match ? bgcolor : null }} key={Math.random()}>{items}</TableCell>
        );
    }
    const { id, __typename, paid, ...rest } = items;
    return (
        Object.values(rest).map(e => (
            <TableCell style={{ color: colorValue, backgroundColor: paid ? bgcolor : null }} key={Math.random()}>{e}</TableCell>
        ))
    );
}


