import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

class ResponsiveDialog extends React.Component {
    render() {
        const { fullScreen, open = false, title, content, negativeHandler = e => null, positiveHandler } = this.props;

        return (
            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={negativeHandler}
                aria-labelledby={title}
            >
                <DialogTitle id={title}>{title}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {content}                        
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={negativeHandler} color="primary" autoFocus>
                        No
                        </Button>
                    <Button onClick={positiveHandler} color="primary">
                        Yes
                        </Button>
                </DialogActions>
            </Dialog>
        );
    }
}

ResponsiveDialog.propTypes = {
    fullScreen: PropTypes.bool.isRequired,
};

export default ResponsiveDialog;


