import React from 'react';
import PropTypes from 'prop-types';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Dialog from './Dialog';

class DeleteDialog extends React.Component {
    state = {
        open: false,
    };

    handleClose = () => {        
        this.setState({ open: false });
    };

    handleOpen = () => {        
        this.setState({ open: true });
    };

    render() {
        const { open } = this.state;
        const { positiveHandler = e => null, negativeHandler = e => null } = this.props;
        return (
            <div>
                <IconButton aria-label="Delete" onClick={this.handleOpen}>
                    <DeleteIcon />
                </IconButton>
                {
                    open ? (
                        <Dialog
                            fullScreen={false}
                            open={open}
                            title={"Confirm Delete"}
                            content={"Are You sure?"}
                            negativeHandler={e => { this.handleClose(e); negativeHandler(e) }}
                            positiveHandler={e => { this.handleClose(e); positiveHandler(e) }}
                        >
                        </Dialog>
                    ) : null
                }
            </div>
        );
    }
}

DeleteDialog.propTypes = {
    fullScreen: PropTypes.bool.isRequired,
};

export default DeleteDialog;


